import { Component } from '@angular/core';
import { ActiveModal } from './modal-ref';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'app-modal-content',
	template: `
		<style>
			.msb-icon {
				font-size: 50px;
			}
			.modal-body .row > div {
				display: flex;
				align-items: center;
			}
		</style>
		<div class="modal-header">
			<h4 class="modal-title">{{title}}</h4>
			<button *ngIf="closeButton" type="button" class="close" aria-label="Close" (click)="activeModal.dismiss('Cross click')">
				<span aria-hidden="true">&times;</span>
			</button>
		</div>
		<div class="modal-body">
			<div *ngIf="icon" class="row">
				<div class="col-1">
					<span *ngIf="icon === 'success'" style="color: #BDC700;"><i class="msb-icon fa fa-check-circle"></i></span>
					<span *ngIf="icon === 'info'" style="color: #bfdaec"><i class="msb-icon fa fa-info-circle"></i></span>
					<span *ngIf="icon === 'warning'" style="color: #ef6500"><i class="msb-icon fa fa-exclamation-triangle"></i></span>
					<span *ngIf="icon === 'danger'" style="color: #d4003c"><i class="msb-icon fa fa-times-circle"></i></span>
				</div>
				<div class="col-11">
					<div style="margin-left: 15px;" [innerHTML]="getDescription()"></div>
				</div>
			</div>

			<div *ngIf="!icon" [innerHTML]="getDescription()"></div>
		</div>
		<div *ngIf="confirmButton && dismissButton && !closeButton" class="modal-footer center-items">
			<button type="button"(click)="confirm()" class="primary" clickShortCutKey="KeyJ">{{translations.yes}}</button>
			<button type="button" (click)="dismiss()" clickShortCutKey="KeyN"> {{translations.no}}</button>
		</div>
		<div *ngIf="!confirmButton && !dismissButton && closeButton" class="modal-footer">
			<button type="button" (click)="close()">{{translations.close}}</button>
		</div>
	`
})
export class MessageBoxWindow {
	public title: string;

	public description: string;

	public icon: 'success' | 'info' | 'warning' | 'danger';

	public closeButton: boolean;

	public confirmButton: boolean;

	public dismissButton: boolean;

	public translations = {
		close: 'Schließen',
		no: 'Nein',
		yes: 'Ja'
	};

	constructor(public activeModal: ActiveModal, private sanitized: DomSanitizer) { }

	public getDescription() {
		return this.sanitized.bypassSecurityTrustHtml(this.description);
	}

	public dismiss() {
		this.activeModal.dismiss(false);
	}

	public confirm() {
		this.activeModal.close(true);
	}

	public close() {
		this.activeModal.close();
	}
}
