import { Component, HostBinding, HostListener, OnInit } from '@angular/core';

@Component({
  selector: 'parallex-layers',
  templateUrl: './parallex-layers.component.html',
  styleUrls: ['./parallex-layers.component.scss']
})
export class ParallexLayersComponent implements OnInit {

  public isDay: boolean;

  @HostBinding('class.daytime') isDayTime: boolean = false;

  public get title(): string {
    return this.isDay ? 'Guten Tag' : 'Guten Abend';
  }

  constructor() { }

  ngOnInit(): void {
    const now = new Date();
    if (now.getHours() < 6 || now.getHours() >= 17) {
      this.isDay = false;
    }
    else {
      this.isDay = true;
      this.isDayTime = true;
    }

    this.onScroll();

  }

  @HostListener('window:scroll', ['$event']) // for window scroll events
  public onScroll() {
    //if (window.scrollY > 1200) return;
    //   return
    //   //   if (window.scrollY > 260) return;
    //   // }
    //   // else {

    //   //   if (window.scrollY > 400) return;
    // }

    let parent: HTMLElement = document.getElementById("parallax-container");
    let children = parent.querySelectorAll('div[parallax-item]');
    for (let i = 0; i < children.length; i++) {
      (children[i] as HTMLElement).style.top = -1 * (window.pageYOffset * (i + 1) / children.length) + (400 / (2 * (i + 1))) + 'px';
    }
  }

}
