import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'parallex-panel',
  templateUrl: './parallex-panel.component.html',
  styleUrls: ['./parallex-panel.component.scss']
})
export class ParallexPanelComponent implements OnInit {

  @Input() height: string;
  @Input() zIndex: string;
  @Input() color: string;
  @Input() background: string;

  constructor() { }

  ngOnInit(): void {

    if (!this.height) {
      this.height = '50vH';
    }
    if (!this.zIndex) {
      this.zIndex = '1';
    }
    if (!this.color) {
      this.color = 'white';
    }
    if (!this.background) {
      this.background = 'transparent';
    }
  }
}
