import { AfterViewInit, Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'hero-text',
  templateUrl: './hero-text.component.html',
  styleUrls: ['./hero-text.component.scss']
})
export class HeroTextComponent {
  @ViewChild('container') container: ElementRef;
  public backgroundPosition = "-10%"

  @Input()
  public text: string;

  @Input()
  public src: string;

  constructor() { }

  @HostListener('window:scroll', ['$event'])
  public onScroll() {
    const rect = this.container.nativeElement.parentElement.getBoundingClientRect();
    const scrollPercentage = ((rect.top) / ((window.innerHeight || document.documentElement.clientHeight))) * 100;
    this.backgroundPosition = `calc(-30% - ${scrollPercentage / 1.2}%)`;
  }

}
