import { Component, Input } from '@angular/core';

@Component({
  selector: 'pulse-pic',
  templateUrl: './pulse-pic.component.html',
  styleUrls: ['./pulse-pic.component.scss']
})
export class PulsePicComponent {
  @Input()
  public src: string;

  @Input()
  public title: string;

  @Input()
  public zoom: boolean = true;

  @Input()
  public size: 'sm' | 'md' | 'md-dyn' | 'lg' = 'lg';

  public showText: boolean;

  constructor() { }

  public toggleTitle() {
    this.showText != this.showText;
  }

  public hideTitle() {
    this.showText = false;
  }
  public showTitle() {
    this.showText = true;
  }
}