import { Directive, ElementRef, HostListener } from '@angular/core';
import { modalOptionExtraLargeAndCenter, Modal } from 'src/app/core/dialog/modal/modal';
import { ImgFullModalComponent } from '../img-full-modal/img-full-modal.component';

@Directive({
  selector: '[preview]'
})
export class ImgPreviewDirective {
  constructor(private host: ElementRef<HTMLImageElement>,
    private modal: Modal) {

  }

  @HostListener('click')
  public onClick() {
    const modalRef = this.modal.open(ImgFullModalComponent, modalOptionExtraLargeAndCenter);
    modalRef.componentInstance.imgSrc = this.host.nativeElement.src;
  }
}