import { Component, ElementRef, Input, OnInit, TemplateRef } from '@angular/core';

export interface ScrollShowItem {
  backgroundText: string;
  foregorundText: string;
  template?: TemplateRef<ElementRef>;
}

@Component({
  selector: 'scroll-show',
  templateUrl: './scroll-show.component.html',
  styleUrls: ['./scroll-show.component.scss']
})
export class ScrollShowComponent implements OnInit {

  @Input()
  public items: ScrollShowItem[];

  @Input()
  public withDescription: boolean;

  constructor() { }

  ngOnInit(): void {
    if (this.items?.length > 10) {
      alert('Mehr als 10 Elemente zeigt die Seite nicht so schön an, sorry')
    } else if (this.withDescription && this.items?.length > 5) {
      alert('Mehr als 5 Elemente zeigt die Seite nicht so schön an, sorry.')
    }
  }

  public scrollToTemplate(index: number): void {
    document.querySelector("[slot=template" + index + "]").scrollIntoView({
      behavior: "smooth",
      block: "start"
    });
  }

}
