import {
  Directive,
  TemplateRef,
  ViewContainerRef,
  Input,
  ElementRef,
} from '@angular/core';
import { VisibilityService } from './visibility.service';
import { take, filter, tap } from 'rxjs/operators';

/*
Verwendung: 

<div #wrapper style="height: 100vH">
  <hello [name]="name" *visibleWith="wrapper"></hello>
</div>
*/

@Directive({ selector: '[visibleWith]' })
export class VisibleWithDirective {
  constructor(
    private templateRef: TemplateRef<any>,
    private viewContainer: ViewContainerRef,
    private visibilityService: VisibilityService
  ) { }

  @Input()
  set visibleWith(element) {
    this.visibilityService
      .elementInSight(new ElementRef(element))
      .pipe(
        tap((visible) => console.log(visible)),
        filter((visible) => visible),
        take(1)
      )
      .subscribe(() => {
        this.viewContainer.createEmbeddedView(this.templateRef);
      });
  }

  private hideElement() { }

  private showElement() { }
}
