import { Injectable, Injector, ComponentFactoryResolver } from '@angular/core';
import { MessageBoxWindow } from './message-box-window';
import { ModalOptions } from './modal-options';
import { ModalRef } from './modal-ref';
import { ModalStack } from './modal-stack';

/**
 * default settings for modals
 */
 export const modalOptionDefaultCenter: ModalOptions = {
	size: 'sm',
	centered: true,
	backdrop: 'static'
  };
  
  /**
   * Settings for large and center modals
   */
  export const modalOptionLargeAndCenter: ModalOptions = {
	size: 'lg',
	centered: true,
	backdrop: 'static'
  };
  
  /**
   * Settings for extra large and center modals
   */
  export const modalOptionExtraLargeAndCenter: ModalOptions = {
	size: 'full',
	centered: true,
	backdrop: 'static'
  };
  

/**
 * 
  const modalRef=this._modal.open(OperationCountInSequenceModalComponent,modalOptionDefaultCenter);
modalRef.componentInstance.xxxx = xxxx
 */
@Injectable()
export class Modal {
	constructor(private _moduleCFR: ComponentFactoryResolver,
		private _injector: Injector,
		private _modalStack: ModalStack) { }

	open(content: any, options: ModalOptions = {}): ModalRef {
		return this._modalStack.open(this._moduleCFR, this._injector, content, options);
	}

	showConfirm(message: string, title: string, options: ModalOptions = {}): Promise<void> {
		options.backdrop = 'static';
		const modalRef = this._modalStack.open(this._moduleCFR, this._injector, MessageBoxWindow, options);
		const msbWnd = <MessageBoxWindow>modalRef.componentInstance;

		msbWnd.description = message;
		msbWnd.title = title;
		msbWnd.confirmButton = true;
		msbWnd.dismissButton = true;

		if (options.translations && options.translations.yes) {
			msbWnd.translations.yes = options.translations.yes;
		}

		if (options.translations && options.translations.no) {
			msbWnd.translations.no = options.translations.no;
		}

		return modalRef.result;
	}

	showMessage(message: string, title: string, icon?: 'success' | 'info' | 'warning' | 'danger', options?: ModalOptions): Promise<void> {
		if (!options) {
			options = {};
		}

		const modalRef = this._modalStack.open(this._moduleCFR, this._injector, MessageBoxWindow, options);
		const msbWnd = <MessageBoxWindow>modalRef.componentInstance;

		msbWnd.description = message;
		msbWnd.title = title;
		msbWnd.closeButton = true;
		msbWnd.icon = icon;

		if (options.translations && options.translations.close) {
			msbWnd.translations.close = options.translations.close;
		}

		return modalRef.result;
	}
}
