import { Component, HostBinding, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ScrollShowItem } from 'src/app/shared/scroll-show/scroll-show.component';
import { AddScrollOut } from 'src/app/shared/factory/add-scroll-out.decorator';
import { AnimationEnum } from 'src/app/shared/factory/animations.enum';
import { getRandom } from 'src/app/shared/factory/random-function';

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.scss']
})
export class PersonalComponent implements OnInit {
  @HostBinding('class.daytime') isDayTime: boolean = false;

  public cheat: boolean = false;
  public scrollShowItems: ScrollShowItem[] = [

    {
      backgroundText: 'Paris',
      foregorundText: 'Ein Leben wie Gott in Frankreich',
    },
    {
      backgroundText: 'Barcelona',
      foregorundText: 'Hat er Berlin gesagt',
    },
    {
      backgroundText: 'Iceland',
      foregorundText: 'Oh mein Gott. Wunderschön.',
    },
    {
      backgroundText: 'Praha',
      foregorundText: 'Krass, Krasser, The Chamber',
    },
    {
      backgroundText: 'Rome',
      foregorundText: 'Vino, Pinsa & Stones',
    }]

  public pictureUrl: string;
  public isDay: boolean;

  public get title(): string {
    return this.isDay ? 'Guten Tag' : 'Guten Abend';
  }

  constructor(private route: ActivatedRoute) {
    const now = new Date();
    if (now.getHours() < 6 || now.getHours() >= 17) {
      this.isDay = false;
    }
    else {
      this.isDay = true;
      this.isDayTime = true;
    }

    const pictureName = this.isDay ? 'day' : 'night';
    const random = getRandom(1, 3);
    this.pictureUrl = '../../../assets/pictures/' + pictureName + random + '.jpg';

  }
  public ngOnInit(): void {
  }

  public darkenBG: boolean = false;
  public handleBgDarken() {
    this.darkenBG = true;
  }

  public handleBgLighter() {
    this.darkenBG = false;
  }
}