import { Component, Input } from '@angular/core';
import { ActiveModal } from 'src/app/core/dialog/modal/modal-ref';

@Component({
  selector: 'img-full-modal',
  templateUrl: './img-full-modal.component.html',
  styleUrls: ['./img-full-modal.component.scss']
})
export class ImgFullModalComponent {

  @Input()
  public imgSrc: string;

  constructor(private activeModal: ActiveModal) { }

  /**
    * closes the modal
    */
  public close(): void {
    this.activeModal.close();
  }
}
