import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FavoriteItemsComponent } from './pages/favorite-items/favorite-items.component';
import { IcelandPageComponent } from './pages/iceland-page/iceland-page.component';
import { PersonalComponent } from './pages/personal/personal.component';
import { IcelandVideoComponent } from './pages/iceland-video/iceland-video.component';
import { ComicComponent } from './pages/comic/comic.component';


const routes: Routes = [
  { path: '', component: PersonalComponent },
  { path: 'favoriteItems', component: FavoriteItemsComponent },
  { path: 'me', component: PersonalComponent },
  { path: 'iceland', component: IcelandPageComponent },
  { path: 'iceland-video', component: IcelandVideoComponent },
  { path: 'comic', component: ComicComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'disabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
