import { DOCUMENT } from '@angular/common';
// tslint:disable-next-line:import-spacing
import {
	ApplicationRef,
	Injectable,
	Injector,
	Inject,
	ComponentFactory,
	ComponentFactoryResolver,
	ComponentRef,
	TemplateRef
} from '@angular/core';
import { ModalBackdrop } from './modal-backdrop';
import { ModalWindow } from './modal-window';
import { ContentRef, ActiveModal, ModalRef } from './modal-ref';

@Injectable()
export class ModalStack {
	private _document: any;
	private _windowAttributes = ['backdrop', 'centered', 'keyboard', 'size', 'windowClass'];
	private _backdropAttributes = ['backdropClass'];

	constructor(
		private _applicationRef: ApplicationRef, private _injector: Injector,
		private _componentFactoryResolver: ComponentFactoryResolver, @Inject(DOCUMENT) document) {
		this._document = document;
	}

	open(moduleCFR: ComponentFactoryResolver, contentInjector: Injector, content: any, options): ModalRef {
		const containerEl = this.isDefined(options.container) ? this._document.querySelector(options.container) : this._document.body;

		if (!containerEl) {
			throw new Error(`The specified modal container "${options.container || 'body'}" was not found in the DOM.`);
		}

		const activeModal = new ActiveModal();
		const contentRef = this._getContentRef(moduleCFR, options.injector || contentInjector, content, activeModal);

		const backdropCmptRef: ComponentRef<ModalBackdrop> =
			options.backdrop !== false ? this._attachBackdrop(containerEl) : null;
		const windowCmptRef: ComponentRef<ModalWindow> = this._attachWindowComponent(containerEl, contentRef);
		const ngbModalRef: ModalRef = new ModalRef(windowCmptRef, contentRef, backdropCmptRef, options.beforeDismiss);

		activeModal.close = (result: any) => { ngbModalRef.close(result); };
		activeModal.dismiss = (reason: any) => { ngbModalRef.dismiss(reason); };

		this._applyWindowOptions(windowCmptRef.instance, options);

		if (backdropCmptRef && backdropCmptRef.instance) {
			this._applyBackdropOptions(backdropCmptRef.instance, options);
		}
		return ngbModalRef;
	}

	private _attachBackdrop(containerEl: any): ComponentRef<ModalBackdrop> {
		const backdropFactory: ComponentFactory<ModalBackdrop> =
			this._componentFactoryResolver.resolveComponentFactory(ModalBackdrop);
		const backdropCmptRef = backdropFactory.create(this._injector);
		this._applicationRef.attachView(backdropCmptRef.hostView);
		containerEl.appendChild(backdropCmptRef.location.nativeElement);
		return backdropCmptRef;
	}

	private _attachWindowComponent(containerEl: any, contentRef: any): ComponentRef<ModalWindow> {
		const windowFactory = this._componentFactoryResolver.resolveComponentFactory(ModalWindow);
		const windowCmptRef = windowFactory.create(this._injector, contentRef.nodes);
		this._applicationRef.attachView(windowCmptRef.hostView);
		containerEl.appendChild(windowCmptRef.location.nativeElement);
		return windowCmptRef;
	}

	private _applyWindowOptions(windowInstance: ModalWindow, options: Object): void {
		this._windowAttributes.forEach((optionName: string) => {
			if (this.isDefined(options[optionName])) {
				windowInstance[optionName] = options[optionName];
			}
		});
	}

	private _applyBackdropOptions(backdropInstance: ModalBackdrop, options: Object): void {
		this._backdropAttributes.forEach((optionName: string) => {
			if (this.isDefined(options[optionName])) {
				backdropInstance[optionName] = options[optionName];
			}
		});
	}

	private _getContentRef(
		moduleCFR: ComponentFactoryResolver, contentInjector: Injector, content: any,
		context: ActiveModal): ContentRef {
		if (!content) {
			return new ContentRef([]);
		} else if (content instanceof TemplateRef) {
			return this._createFromTemplateRef(content, context);
		} else if (this.isString(content)) {
			return this._createFromString(content);
		} else {
			return this._createFromComponent(moduleCFR, contentInjector, content, context);
		}
	}

	private _createFromTemplateRef(content: TemplateRef<any>, context: ActiveModal): ContentRef {
		const viewRef = content.createEmbeddedView(context);
		this._applicationRef.attachView(viewRef);
		return new ContentRef([viewRef.rootNodes], viewRef);
	}

	private _createFromString(content: string): ContentRef {
		const component = this._document.createTextNode(`${content}`);
		return new ContentRef([[component]]);
	}

	private _createFromComponent(
		moduleCFR: ComponentFactoryResolver, contentInjector: Injector, content: any,
		context: ActiveModal): ContentRef {
		const contentCmptFactory = moduleCFR.resolveComponentFactory(content);
		const modalContentInjector = Injector.create([{ provide: ActiveModal, useValue: context }], contentInjector);
		const componentRef = contentCmptFactory.create(modalContentInjector);
		this._applicationRef.attachView(componentRef.hostView);
		return new ContentRef([[componentRef.location.nativeElement]], componentRef.hostView, componentRef);
	}

	private isDefined(value: any): boolean {
		return value !== undefined && value !== null;
	}

	private isString(value: any): boolean {
		return typeof value === 'string';
	}
}
