import { Component, HostListener, Input, OnInit } from '@angular/core';
import { Modal, modalOptionLargeAndCenter } from 'src/app/core/dialog/modal/modal';
import { TableModalComponent } from 'src/app/core/dialog/modal/table-modal/table-modal.component';
import { favoriteGames } from './favoriteGames';
import { CategoryEnum, Game } from './game.interface';



@Component({
  selector: 'favorite-items',
  templateUrl: './favorite-items.component.html',
  styleUrls: ['./favorite-items.component.scss']
})
export class FavoriteItemsComponent implements OnInit {
  @Input()
  public showNotMine: boolean = true;

  public enumKeys(): Array<string> {
    var keys = Object.keys(CategoryEnum);
    return keys.slice(keys.length / 2);
  };

  public breakIndex: number;
  public filterCategory?: any;
  public favItems: Game[] = favoriteGames

  @HostListener('window:resize', ['$event'])
  onResize() {
    this.calculateBreakIndex();
  }

  constructor(private _modal: Modal) { }

  ngOnInit(): void {
    this.calculateBreakIndex();
  }

  public filterItems(): any[] {
    return this.favItems;
    return this.favItems.filter(p => !this.filterCategory || p.category == this.filterCategory?.key)
  }

  public setFilter(category: CategoryEnum) {
    if (this.filterCategory === category) {
      this.filterCategory = null;
    }
    else {
      this.filterCategory = category;
    }
  }

  public openListModal() {
    const modalRef = this._modal.open(TableModalComponent, modalOptionLargeAndCenter);
    modalRef.componentInstance.list = this.favItems.map((item) => item.tagLine);
  }

  private calculateBreakIndex() {
    this.breakIndex = window.innerWidth < 1000 ? (window.innerWidth < 768 ? (window.innerWidth < 550 ? 2 : 3) : 4) : 5;
  }
}
